@import url('https://fonts.googleapis.com/css2?family=Quicksand:wght@300&family=Space+Mono:ital,wght@0,400;0,700;1,400;1,700&display=swap');


.App {
  text-align: center;
  background-color: #000000;
  overflow: hidden;
  cursor: default;
}

.App-header {
  
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-title{
  font-size: 1.5em;
}

.App-subtitle{
  color: #ffafcc;
  font-size: smaller;
}

.App-link {
  color: #61dafb;
}

.Navbar{
  /* gradient : */
  background: #000;
  color: #b3d3db;
  font-size: 1.5em;
  padding: 0.5em;
  height: 2em;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.Navbar-button{
  color: #b3d3db;
  font-size: 2em;
  padding: 0.1em;
  height: 2em;
}

.Navbar-brand{
  color: #b3d3db;
  font-size: .5em;
  position: relative;
  top: -1em;
}

.Scroll-Down{
  color: #EBB9D2;
  font-size: .5em;
  padding: 0.1em;
  height: 1.5em;
  /* le mettre en bas à droite */
  position: absolute;
  bottom: 0;
  /* le réhausser */
  top: 90%;
  right: 0;
  /* rotation de 90deg */
  transform: rotate(90deg);
}

.Scroll-Down-arrow{
  width: 3px;
  height: 90px;
  background: #EBB9D2;
  position: absolute;
  top: -250%;
  left: 50%;
  margin-left: -1.5px;
  /* rotation */
  transform: rotate(90deg);
}

.Navbar-menu{
  color: #b3d3db;
  font-size: 1.5em;
  padding: 0.1em;
  height: 2em;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}


.sep{
    height: 1px;
    background: #b3d3db;
    margin: 1em 0;
    opacity: 0.5;
}
.sep2{
    height: 1px;
    background: #b3d3db;
    margin: 1em 0;
    opacity: 0.5;
}

.BigTitle{
  font-size: 3em;
  color: white;
  font-weight: 400;
}

.BigTitle:hover{
  color: #b3d3db;
}

.buttons{
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  width: 7%;
  /* mettre en bas de la page */
  position: absolute;
  bottom: 0;

}

.button{
  color: #cdb4db;
  font-size: .5em;
  padding: 0.1em;
  height: 2em;
  font-family: 'Space Mono', monospace;
  text-decoration: none;
}

.button:hover{
  color: #FFF;
}


/* responsive */
@media screen and (max-width: 600px) {
  .BigTitle{
    font-size: 1.5em;
  }
  .buttons{
    width: 30%;
  }
  .button{
    font-size: .8em;
  }
}